import React, { useState, useEffect } from 'react'
import News from './Pages/News';

const App = () => {
    const [news, setNews] = useState([]);

    useEffect(() => {
        fetch('https://api.currentsapi.services/v1/latest-news?language=en&apiKey=OWrdipYQ9gk4eQq1va3jzocqrS7xvVQTxAbLhCLkocLijp98')
            .then(res => res.json())
            .then(data => {
                //console.log(data.news)
                setNews(data.news)
            })
    }, [])

    return (
        <>
            {/* <div className="head">
                <div className="headerobjectswrapper">
                    <header>Daily Rophet</header>
                </div>
                <div className="subhead">Asgardia, Space - {`${new Date().getDay()}/${new Date().getMonth()}/${new Date().getFullYear()}`}</div>
            </div> */}
            <div className="news-page_section publisher">
                <div className="logo">
                    <img src="http://redonion.se/cssgrid/images/logo-shield155.png" alt=""/>
                </div>
                <div className="publisher_name">
                    <img src="http://redonion.se/cssgrid/images/logo-cleanup.svg" alt=""/>
                    <div class ="publisher_tagline">THE WIZARD WORLD'S BEGUILING BROADSHEET OF CHOICE</div>
                </div>
            </div>
            <div className="content">




                <div className="collumns">

                    {
                        news.map(single => (
                            <News key={single.id} news={single} />
                        ))
                    }


                </div>
            </div>
        </>
    )
}

export default App
