import React, { useEffect, useState } from 'react'

function News({ news }) {

    const [image, setImage] = useState('')

    const getGif = (title) => {
        let gettitle = title.split(/\s/);
        let last = `${gettitle[0]} ${gettitle[1]}`
        fetch(`https://api.giphy.com/v1/gifs/search?q=${last}&api_key=hQ2HrdQhqkzjxM8WBEJaiENKAfOn6RKO&limit=1`)
            .then(res => res.json())
            .then(data => {
                setImage(data.data[0].images.original.url)
            })
    }

    useEffect(() => {

        getGif(news.title)

    }, [])


    return (
        <div className="collumn">
            <div className="head">
            <a href={news.url}><span className="headline hl1">{news.title}</span></a>
                <p><span className="headline hl2">{news.author}</span></p>
            </div>
            <figure className="figure">
                <a href={news.url} target="_blank">
                    <img className="media" src={image} alt="" />
                </a>
                <figcaption className="figcaption">"This time, let go your conscious self and act on instinct."</figcaption>
            </figure>

            <p className="lead"> {news.description}</p>


        </div>
    )
}

export default News
